import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import Button from '../components/Button.jsx';
import GMap from '../components/Map.jsx';
import { Header,
  Grid, Container, Form, Input, TextArea, Image } from 'semantic-ui-react';
import FontAwesome from 'react-fontawesome';
import image from '../../assets/images/map-placeholder.png';

export default class Contact extends React.Component {
  render() {
    return <div id='contact'>
             <section className='phone hero'>
               <Container>
                 <h1 className='heading'>+36 30 219 2311</h1>
                 <h3>Krjen idpontot!</h3>
               </Container>
             </section>
             <section>
               <Container>
                 <Grid stackable columns={ 2 }>
                   <Grid.Column className='contact-info'>
                     <h3>Dr. Varga Klra</h3>
                     <div>
                       <FontAwesome name='map-marker'></FontAwesome>
                     </div>
                     <div>
                       1037 Budapest, Montevideo utca 16b
                     </div>
                     <div>
                       <FontAwesome name='phone'></FontAwesome>
                     </div>
                     <div>
                       +36 30 219 2311
                     </div>
                     <div>
                       <FontAwesome name='envelope'></FontAwesome>
                     </div>
                     <div>
                       info@vargaklara.hu
                     </div>
                   </Grid.Column>
                   <Grid.Column className='contact-form'>
                    <h3>Krdse van?</h3>
                     <Form>
                       <Form.Group widths='equal'>
                         <Form.Field control={ Input } label='Teljes nv' placeholder='Adja meg teljes nevt' />
                         <Form.Field control={ Input } label='Email cm' placeholder='Adja meg email cmt' />
                       </Form.Group>
                       <Form.Field id='form-textarea-control-opinion' control={ TextArea } label='zenet' placeholder='Tegye fel krdst' />
                       <Button primary type='send'/>
                     </Form>
                   </Grid.Column>
                 </Grid>
               </Container>
             </section>
             <section className='map'>
               <GMap />
             </section>
           </div>
  }
}



